*{
  font-family: Roboto;
}

body{

}

.App {
  background-color: #1E1E1E;
  width: 100%;
  top: 0;
  padding-left: 64px;
  min-height: 100vh;
  background-position: center;
  height: 100%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  background-attachment: fixed;
  background-position: calc(50% - -36px) calc(100% - 50%);
}

*{
  text-decoration: none;
}

h1{
  text-align: center;
  color: white;
}

.sidebar-container{
  width: 64px;
  height: 100vh;
  background-color: #333333;
  position: fixed;
  z-index: 99;
  top: 0;
}




.settings-icon{
  position: absolute;
  bottom: 20px;
  p{
    display: none;
  }
}

.app-container{
  display: inline-flex;
  width: 100%;
}

.menu-icon-cont{
  display: inline-block;
  justify-content: center;
  border-width: 2px;
  padding: 1rem 0.7rem;
  border-left: solid #333333;
  border-width: 2px;

}
.menu-icon{
  width: 30px;
}

.icon-subs{
  color: #767676;
  margin: 0;
  font-size: 12px;
  text-align: center;
  margin-top: -16px;
}

.active{
  color: red;
  p{
    color: white;
    transform: scale(1.03);
    transition: color 0.5s;
  }
  div{
    border-left: solid #ffffff;
    border-width: 2px;
    transition: all 0.5s;
  }
  path{
    fill: white;
    transition: all 0.5s;
  }
  svg{
    overflow: hidden;
    transform: scale(1.03);
    
  }
}

svg{
  transition: 0.25s;
}

.hero-container{
  width: 100%;
  display: inline-flex;
}

.hero-content-container{
  width: 100%;
  height: 100vh;
  
}

.hero-image-container{
  width: 100%;
  height: 100vh;
  max-width: 510px;
  background-image: url('./images/profile-pic.png');
  background-size: cover;
    background-position: center;

}

.social-icons-container{
  width: 10%;
  height: 100vh;
  min-width: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img{
    width: 32px;
    padding: 14px 0px;
  }
  
}

.test{
  height:500px
}

//animated hero content

.hero-sub{
  color: white;
  margin-top: 434px;
  margin-left: 87px;
}

.animated-header-hero{
  position: relative;
  margin-left: -35px;
  margin-top: -57px;

  .timothy-container{
    height: 130px;
    width: 557px;
    overflow: hidden;
    position: absolute;
    top: 123px;
    z-index: 1;
  }
  .header-timothy{
    position: absolute;
    font-size: 111px;
    margin: 0;
    top: -1px;
    left: 138px;
    animation: timothy;
    animation-duration: 1.5s;
  }
  .eye-tim{
    padding-left: 6px;
    padding-right: 7px;

  }
  .dot-tim{
    position: absolute;
    top: -66px;
    left: 73px;
    animation: dot;
    animation-duration: 1.5s;
  }
  .williams-container{
    z-index: 1;
    height: 115px;
    width: 868px;
    overflow: hidden;
    position: absolute;
    top: 231px;
  }
  .header-williams{
    position: absolute;
    font-size: 148px;
    margin: 0;
    top: -29px;
    left: 213px;    
    animation: williams;
    animation-duration: 0.8s;
  }
  .fowler-container{
     height: 92px;
    width: 868px;
    overflow: hidden;
    position: absolute;
    top: 325px;
    z-index: 1;
  }
  .header-fowler{
    position: absolute;
    font-size: 109px;
    margin: 0;
    left: 117px;
    top: -18px;
    animation: fowler;
    animation-duration: 1s;
  }
  .space-fowler{
    margin-left: -23px;
  }

}

.tim-blurb{
  color: white;
  margin-left: 88px;
    /* margin-right: 75px; */
    margin-top: 22px;
    padding-top: 19px;
    border-top: solid white;
    border-width: 1px;
    letter-spacing: 0.01em;
    line-height: 1.5;
}


@keyframes timothy{
  0% {padding-top: 100px;}
  50% {padding-top: 0px; opacity: 1}
}


  @keyframes dot{

    0% {padding-top: 100px;}
    45% {top:-72px}
    65% {top: -66px}
    45% {padding-top: 0px; opacity: 1}
   

  }

@keyframes williams{
  0% {padding-top: 150px; }
  45% {padding-top: 150px; }
  70% {padding-top: -50px; opacity: 1; }
}

@keyframes fowler{
  0% {padding-top: 150px; }
  70% {padding-top: 150px; }
  95% {padding-top: -50px; opacity: 1; }
}



.page{
  top: 0;
  position: absolute;
  left: 0;
  right: 0;
overflow: hidden;
}

// skills

.skills-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 12px;
  min-height: 100vh;
}

.skills-container{
  display: inline-flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;

}

.skills-section{
  color: white;
  text-align: center;
  padding: 20px 20px;
}

.skills-row{
  display: flex;
}

.skills-donut{
  width: 96px;
  p{
    font-size: 12px;
    white-space: nowrap;
  }
}

/// works

.works-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.works-container::after {
  content: "";
  margin-left: 372px;
}

.Works-card-cont{
  color: white;
  height: 441px;
  width: 300px;
  background-color: #2b2b2b;
  margin: 36px;
  border-radius: 5px;
  position: relative;
  .word-card-header{
    text-align: center;
    margin: 0;
    padding: 12px;
  }
  img{
    width: 300px;
    border-radius: 5px;
  }
  .work-card-desc{
    padding: 10px 20px;
    margin: 0;
  }
  .work-card-badges{
    display: flex;
    padding: 2px 12px;
    flex-wrap: wrap;
    p{
      background-color: #29427c;
      padding: 2px 5px;
      margin: 5px;
      border-radius: 5px;
      font-size: 12px;
    }
    .design-badge{
      background-color: #226622;
    }
    .misc-badge{
      background-color: black;
    }
  }
  .works-card-links{
    position: absolute;
    bottom: 0;
    height: 50px;
    display: inline-flex;
    width: inherit;
    padding: 12px;
    img{
      width: 28px;
      padding: 12px 5px;
    }

  }
}

.popover{
  color: white;
  background-color: #171414;
  padding: 20px;
  border-radius: 5px;
  z-index: 99;
  width: 400px;
  max-width: 80vw;
  margin: auto;
  h3{
    border-top: solid white;
    border-width: 1px;
    padding-top: 10px;
  }
  p{
    font-size: 14px;
  }
  
}

.tooltip{
  z-index: 999 !important;
}



.sidebar-container-light{
  background-color: #cecece47;
  .menu-icon-cont{
    border-left: solid #cecece;
    border-width: 2px;
  }
  .active div {
    border-left: solid black;
    border-width: 2px;
  }
  .active path {
    fill: black;
  }
  .active p {
    color: black;
  }
  path{
    fill: #766c6c;
  }
}


.App-light{
  background-color: #cecece4f;
  background-image: url('/images/twf-white.png');
  .hero-image-container {
    background-image: url('/images/tim-bg-darker.png');
  }
  h1{
    color: black
  }
  rect{
    fill: black
  }
  ellipse{
    fill: black
  }
  .hero-sub{
    color: black;
  }
  .tim-blurb{
    color: black;
    border-top: solid black;
    border-width: 1px;
  }
  .Works-card-cont {
    p{
      color: white;
    }
  }
  .Works-card-cont {
    color: black;
    background-color: #ffffff;
}
.skills-section {
  color: black;
}
}

// screen sizes


/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
/* Styles */
}

/* Smartphones (landscape) ----------- */
@media only screen 
and (max-width : 421px) {
  .hero-image-container{
    background-position-x: -53px !important;
  }
}

/* Smartphones (portrait) ----------- */
@media only screen 
and (max-width : 600px) {
  .App{
    padding-left: 0;
    padding-bottom: 70px;
  }
  .sidebar-container {
    bottom: 0;
    top: auto;
    width: 100%;
    height: 74px;
    background-color: #333333;
    position: fixed;
    z-index: 99;
    display: inline-flex;
    justify-content: space-between;
    svg{
      margin-top: -7px;
    }
  }
  .menu-icon-cont {
    border-top: solid #333333;
    border-left: none;
  }
  .sidebar-container a:nth-child(1) { 
    padding-left: 10px;
  }
  .sidebar-container a:nth-child(4) { 
    padding-right: 10px;
  }
  .active div {
    border-top: solid #ffffff;
    border-left: none;
  }
  .active svg {
    margin-top: -7px;
  }
  .tooltip {
    z-index: 1 !important;
    margin-left: 12px;
  }
  .hero-container {
    display: inline;
  }
  .hero-content-container {
    width: 100%;
    height: 100vh;
    /* margin-bottom: 78px; */
    position: absolute;
    z-index: 1;
    
  }
  .hero-image-container {
    width: 100%;
    height: 100vh;
    max-width: 1000px;
    /* height: 604px; */
    /* position: absolute; */
    /* margin-bottom: -78px; */
    top: 0;
    /* max-width: 510px; */
    background-image: url(/static/media/profile-pic.cec09a75.png);
    background-size: cover;
    background-position: center;
 
  }
  .animated-header-hero {
    display: none;
}
